import React, { Component, Fragment } from 'react';
import { Card, Spin, Divider } from 'antd';
import { CreateList } from 'mogul';
import { showUser } from './services/api';

class Detail extends Component {
  state = {
    source: undefined
  };
  componentDidMount() {
    this.showUser();
  }
  columns = [
    {
      title: '姓名',
      key: 'name',
      render: ({ name: { first, last } = {} }) => (
        <div>{`${first} ${last}`}</div>
      )
    },
    {
      title: '性别',
      key: 'gender',
      render: ({ gender }) => <div>{gender === 'female' ? '女' : '男'}</div>
    },
    {
      title: '电话号码',
      dataIndex: 'phone'
    },
    {
      title: 'email',
      dataIndex: 'email'
    },
    {
      title: '注册时间',
      dataIndex: 'registered'
    },
    {
      title: '邮编',
      key: 'postcode',
      render: ({ location: { postcode } }) => <div>{postcode}</div>
    }
  ];

  async showUser() {
    const response = await showUser();
    const source = response.results[0];
    this.setState({
      source
    });
  }
  render() {
    const { source } = this.state;
    const spinStyle = {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center'
    };
    let content = (
      <div style={spinStyle}>
        <Spin />
      </div>
    );
    if (source !== undefined) {
      content = (
        <Fragment>
          <img src={source.picture.large} alt="图片" />
          <Divider />
          {CreateList(this.columns, {
            labelCol: 8,
            contentCol: 16,
            column: 4
          })(source)}
        </Fragment>
      );
    }
    return <Card title="详情">{content}</Card>;
  }
}

export default Detail;



