import axios from 'axios';
import { message } from 'antd';
import { filter } from 'mogul';
import { SYSTEM_PREFIX } from '../utils/env';

const codeMessage = {
  200: '服务器成功返回请求的数据。',
  201: '新建或修改数据成功。',
  202: '一个请求已经进入后台排队（异步任务）。',
  204: '删除数据成功。',
  400: '发出的请求有错误，服务器没有进行新建或修改数据的操作。',
  401: '用户没有权限（令牌、用户名、密码错误）。',
  403: '用户得到授权，但是访问是被禁止的。',
  404: '发出的请求针对的是不存在的记录，服务器没有进行操作。',
  406: '请求的格式不可得。',
  410: '请求的资源被永久删除，且不会再得到的。',
  422: '当创建一个对象时，发生一个验证错误。',
  500: '服务器发生错误，请检查服务器。',
  502: '网关错误。',
  503: '服务不可用，服务器暂时过载或维护。',
  504: '网关超时。'
};

const request = (
  url,
  { headers = {}, body, method = 'GET', params = {}, data = {} } = {}
) => {
  const isFormData = body instanceof FormData;
  const token = localStorage.getItem(`${SYSTEM_PREFIX}_TOKEN`);
  const config = {
    method,
    params,
    data: body !== undefined ? body : data,
    headers: {
      Accept: 'application/json',
      'Content-Type': isFormData
        ? 'multipart/form-data'
        : 'application/json; charset=utf-8',
      token: token === null ? undefined : token,
      client: 'xhr',
      ...headers
    }
  };
  return (
    axios
      .create(config)(url)
      // 所有返回的数据中的 null 和 '' 会被递归修改成 undefined
      .then(({ data }) => filter(data))
      .catch(error => {
        const { response } = error;
        let { status, data = {} } = response;
        data = filter(data);
        const { message: msg = `${status} : ${codeMessage[status]}` } = data;
        message.error(msg);
        return Promise.reject(error);
      })
  );
};

export default request;



