import { observable, action, configure } from 'mobx';

configure({ enforceActions: true });

class BaseCollectionStore {
  constructor({ childrenAlias, options = {} } = {}) {
    if (childrenAlias !== undefined) {
      this.childrenAlias = childrenAlias;
    }
    this.options = options;
  }
  @observable loading = false;
  @observable list = [];
  @observable options = {};
  @observable
  pagination = {
    current: 1,
    pageSize: 10,
    total: 0
  };

  childrenAlias = 'children';

  @action
  showLoading() {
    this.loading = true;
  }

  @action
  hideLoading() {
    this.loading = false;
  }

  @action
  initList({ list = this.list, pagination = this.pagination }) {
    this.list = list;
    this.pagination = pagination;
  }

  @action
  mergeListItem({ id, subId, ...others }) {
    this.list = this.list.map(item => {
      if (subId !== undefined) {
        if (item.id === id) {
          item[this.childrenAlias] = item[this.childrenAlias].map(subItem => {
            if (subItem.id === subId) {
              subItem = Object.assign({}, subItem, others);
            }
            return subItem;
          });
        }
      } else if (item.id === id) {
        item = Object.assign({}, item, others);
      }
      return item;
    });
  }

  @action
  deleteListItem({ id, subId, ...others }) {
    if (subId !== undefined) {
      this.list = this.list.map(item => {
        if (item.id === id) {
          item[this.childrenAlias] = item[this.childrenAlias].filter(
            subItem => subItem.id !== subId
          );
        }
        return item;
      });
    } else {
      this.list = this.list.filter(item => item.id !== id);
    }
  }

  @action
  initOptions(nextOptions) {
    this.options = Object.assign({}, this.options, nextOptions);
  }
}

export default BaseCollectionStore;



