import request from '../../../utils/request';

export const listUsers = ({ page, size }) => {
  return request('https://randomuser.me/api/', {
    params: {
      page,
      results: size,
      seed: 'abc'
    }
  });
};

export const showUser = () => {
  return request('https://randomuser.me/api/');
};



