import React from 'react';
import { Dashboard } from 'mogul';
import { Route, Switch } from 'react-router';
import { SYSTEM_FOOTER, SYSTEM_NAME } from '../../utils/env';
import routing from '../../utils/routing';
import menu from './menu';
import styles from './styles.module.css';
import Tool from './Tool';
import Homepage from '../Homepage';
import Template from '../Template';
// do not delete this Note!
// insert:component

export default ({ match: { url } }) => (
  <Dashboard
    tool={<Tool />}
    routing={routing}
    name={SYSTEM_NAME}
    footer={SYSTEM_FOOTER}
    menu={menu}
    logo={<div className={styles.logo} />}
    url={url}
  >
    <Switch>
      <Route path={`${url}`} component={Homepage} exact />
      <Route path={`${url}/template`} component={Template} />
      {/*do not delete this Note!*/}
      {/*insert:route*/}
    </Switch>
  </Dashboard>
);



