import React from 'react';
import { Popover, Icon, Dropdown, Menu, Button } from 'antd';
import routing from '../../utils/routing';
import styles from './styles.module.css';

const Tool = () => {
  const menu = (
    <Menu>
      <Menu.Item className={styles.menuItem}>
        <Icon type="logout" className={styles.icon} />退出登录
      </Menu.Item>
    </Menu>
  );
  const catalog = <div>制作中……</div>;
  const {
    history: { goBack }
  } = routing;
  const onReturn = () => {
    goBack();
  };
  return (
    <div className={styles.tools}>
      <div className={styles.left}>
        <Button icon="left" onClick={onReturn}>
          返回
        </Button>
      </div>
      <div className={styles.right}>
        <Popover title="中后台大目录" content={catalog}>
          <div className={styles.trigger}>
            <Icon type="bars" className={styles.icon} />
          </div>
        </Popover>
        <Popover
          title="产品经理企业微信"
          content={<div className={styles.qr} />}
        >
          <div className={styles.trigger}>报告异常</div>
        </Popover>
        <Dropdown overlay={menu}>
          <div className={styles.trigger}>
            <Icon className={styles.icon} type="user" />尤志刚-油麦菜
          </div>
        </Dropdown>
      </div>
    </div>
  );
};

export default Tool;



