export default [
  {
    title: 'Demo',
    icon: 'appstore',
    children: [
      {
        title: 'Template',
        path: '/template'
      }
    ]
  },
  {
    title: 'navigator-main-1',
    icon: 'ant-design',
    children: [
      {
        title: 'navigator-sub-1',
        path: '/navigator-sub-1'
      },
      {
        title: 'navigator-sub-2',
        path: '/navigator-sub-2'
      }
    ]
  },
  {
    title: 'navigator-main-2',
    path: '/navigator-main-2',
    icon: 'google'
  }
];



